.Main-container {
    height: 100vh;
    width: 100vw;
}

.Video-container.mobile {
    background-color: #e6e6e6;
    position: relative;
    width: 100vw;
    height: calc(100vw * (640 / 480));
    float: top;
}
  
.Video-container.desktop {
    background-color: #e6e6e6;
    position: relative;
    width: calc(100vh * (640 / 480));
    height: 100vh;
    float: left;
}

.InfoBox-container.mobile {
    background-color: #ff0000;
    position: relative;
    width: 100vw;
    height: calc(100vh - (100vw * (640 / 480)));
    float: top;
}
  
.InfoBox-container.desktop {
    position: relative;
    width: calc(100vw - (100vh * (640 / 480)));
    height: 100vh;
    float: left;
}
